<template>
  <div id="slider">
        <div class="arrowLeft" @click="arrowLeft()"></div>
   <Slides :image="images[chosenImage]"/>
        <div class="arrowRight" @click="arrowRight()"></div>
  </div>
</template>

<script>
import Slides from "./Slides.vue"

export default {
    components : {
        Slides
    },
    data(){
        return{
            images : [
                {
                    id: 0,
                    url : require("../../images/0.jpeg"),
                    title : "zero"
                },
                {
                    id: 1,
                    url : require("../../images/1.jpeg"),
                    title : "un"
                },
                {
                    id: 2,
                    url : require("../../images/2.jpeg"),
                    title : "deux"
                },
                {
                    id: 3,
                    url : require("../../images/3.jpeg"),
                    title : "trois"
                },
                {
                    id: 4,
                    url : require("../../images/4.jpeg"),
                    title : "quatre"
                }
            ],
            chosenImage : 0,
            intervalObject : null
        }
    },
    methods :{
        arrowLeft(){
            clearInterval(this.intervalObject);
            this.moveLeft();
            var self= this;
            this.intervalObject  = setInterval(() => {
            self.moveLeft();
            },4000);
        },
        arrowRight(){
            clearInterval(this.intervalObject);
            this.moveRight();
            var self= this;
            this.intervalObject  = setInterval(() => {
            self.moveRight();
            },4000);
        },

        moveLeft(){
            var flag = this.chosenImage;
            flag--;
            if(flag< 0){
                flag= (this.images.length -1);
            }
            this.chosenImage = flag;
        },
        moveRight(){
            var flag = this.chosenImage;
            flag++;
            if(flag>= this.images.length){
                flag=0;
            }
            this.chosenImage = flag;
        }
    },
    created(){
        var self= this;
        this.intervalObject  = setInterval(() => {
            self.moveLeft();
        },4000);
    }


}
</script>

<style lang="scss">
#slider{
    position:relative;
}

#slider .arrowLeft, #slider .arrowRight{
    position:absolute;
    top: 50%;
    border: 20px solid transparent;
}
#slider .arrowLeft{
    border-right-color: green;
    z-index:1;
}
#slider .arrowRight{
    right: 0;
    border-left-color:green ;

}


</style>



